import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Observable } from 'rxjs';
import { ConfirmationDialogComponent, ConfirmationDialogData } from '../components/confirmation-dialog/confirmation-dialog.component';

@Injectable({
  providedIn: 'root'
})
export class ConfirmationDialogService {
  constructor(private dialog: MatDialog) {}

  /**
   * 확인 다이얼로그를 표시합니다.
   * @param message 표시할 메시지
   * @param title 다이얼로그 제목 (기본값: '확인')
   * @param confirmText 확인 버튼 텍스트 (기본값: '확인')
   * @param cancelText 취소 버튼 텍스트 (기본값: '취소')
   * @param confirmColor 확인 버튼 색상 (기본값: 'primary')
   * @returns Observable<boolean> - 확인(true) 또는 취소(false)
   */
  confirm(
    message: string,
    title?: string,
    confirmText?: string,
    cancelText?: string,
    confirmColor?: 'primary' | 'accent' | 'warn'
  ): Observable<boolean> {
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      width: '400px',
      maxWidth: '90vw',
      data: {
        title,
        message,
        confirmText,
        cancelText,
        confirmColor
      } as ConfirmationDialogData,
      disableClose: false,
      autoFocus: true,
      panelClass: 'confirmation-dialog-panel'
    });

    return dialogRef.afterClosed();
  }

  /**
   * 삭제 확인 다이얼로그를 표시합니다.
   * @param itemName 삭제할 항목 이름
   * @returns Observable<boolean>
   */
  confirmDelete(itemName?: string): Observable<boolean> {
    const message = itemName 
      ? `${itemName}을(를) 삭제하시겠습니까?`
      : '선택한 항목을 삭제하시겠습니까?';
    
    return this.confirm(message, '삭제 확인', '삭제', '취소', 'warn');
  }

  /**
   * 로그아웃 확인 다이얼로그를 표시합니다.
   * @returns Observable<boolean>
   */
  confirmLogout(): Observable<boolean> {
    return this.confirm('로그아웃 하시겠습니까?', '로그아웃', '로그아웃', '취소', 'warn');
  }
}
